.wrapper{
    display: grid;
    grid-template-columns: 280px auto;
    gap: 30px;
}

.title{
    font-weight: 600;
    font-size: 24px;
    color: #333333;
}

.info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-column: 1/3;
}

.list{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
}

.btn{
    color: #4F4F4F;
    padding: 10px 25px;
    background: #F2F2F2;
    border-radius: 30px;
    display: block;
    margin: 0 auto;
    margin-top: 30px;
}

@media (max-width: 1200px){
    .list{
        grid-template-columns: repeat(3, 1fr);
    }

    .wrapper{
        grid-template-columns: 250px auto;
        gap: 30px 15px;
    }
}

@media (max-width: 992px){
    .list{
        grid-template-columns: repeat(4, 1fr);
    }
    
    .wrapper{
        display: flex;
        flex-direction: column;
    }

    .wrapper{
        row-gap: 25px;
    }
}

@media (max-width: 762px){
    .list{
        grid-template-columns: repeat(3, 1fr);
    }

    .info{
        flex-direction: column;
        align-items: flex-start;
        row-gap: 24px;
    }

    .wrapper{
        row-gap: 20px;
    }
}

@media (max-width: 576px){
    .wrapper{
        row-gap: 15px;
    }
}

@media (max-width: 475px){
    .list{
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 475px){
    .list{
        grid-template-columns: 1fr;
        max-width: 250px;
        margin: 0 auto;
    }
}