.info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
}

.heading{
    font-weight: 600;
    font-size: 24px;
    color: #333333;
}

.list{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 30px;
}

.wrap{
    max-width: 325px;
    width: 100%;
    margin: 150px auto 100px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.desc{
    margin: 30px 0;
}

.title{
    font-weight: 600;
    color: #333333;
    margin-bottom: 10px;
    text-align: center;
}

.subtitle{
    font-weight: 400;
    font-size: 14px;
    color: #4F4F4F;
    text-align: center;
}

@media (max-width: 1200px){

    .wrap{
        margin: 145px auto 100px auto;
    }

    .list{
        grid-template-columns: repeat(4, 1fr);
    }
}

@media (max-width: 992px){

    .wrap{
        margin: 105px auto 75px auto;
    }

    .list{
        grid-template-columns: repeat(3, 1fr);
        gap: 25px;
    }

    .info{
        margin-bottom: 25px;
    }
}

@media (max-width: 762px){
    .wrap{
        margin: 65px auto 50px auto;
    }

    .info{
        flex-direction: column;
        align-items: flex-start;
        row-gap: 20px;
        margin-bottom: 20px;
    }

    .list{
        gap: 20px;
    }
}

@media (max-width: 576px){
    .wrap{
        margin: 25px auto;
    }

    .list{
        grid-template-columns: repeat(2, 1fr);
        max-width: 450px;
        margin: 0 auto;
        gap: 15px;
    }

    .info{
        margin-bottom: 15px;
    }
}

@media (max-width: 425px){
    .list{
        max-width: 250px;
        grid-template-columns: 1fr;
        gap: 10px;
    }
}