.button{
    display: block;
    background: #2585CD;
    border-radius: 15px;
    color: #fff;
    padding: 15px 30px;
    box-shadow: 0 5px 0 #1C649B;
    transition: .5s all;
}

.transparent{
    background: none;
    border: 1px solid #2585CD;
    color: #2585CD;
}

.gray{
    background: #F2F2F2;
    color: #4F4F4F;
    box-shadow: 0 5px 0 #E0E0E0;
}

.button:active{
    box-shadow: 0 2px 0 #1C649B;
    transform: translateY(3px);
}

.gray:active{
    box-shadow: 0 2px 0 #E0E0E0;
}

.card{
    padding: 15px 70px;
    border-radius: 25px;
}

.information{
    color: #2585CD;
    padding: 10px 30px;
    border: 1px solid #2585CD;
    border-radius: 35px;
}

.basket{
    font-size: 14px;
}

.header{
    padding: 10px 20px;
    display: flex;
    column-gap: 10px;
    align-items: center;
    box-shadow: 0 5px 0 #1C649B;
}

.showMore{
    margin: 0 auto;
    padding: 10px 20px;
}

.back{
    font-size: 14px;
    border-radius: 25px;
}




.aside{
    font-size: 14px;
    padding: 15px 50px;
    background: #2585CD;
    border-radius: 30px;
    color: #fff;
}

.aside.unActive{
    opacity: 0.5;
}

@media (max-width: 1200px){
}

@media (max-width: 992px){
    .header{
        display: none;
    }
}

@media (max-width: 762px){
    .information{
        margin: 0 auto;
    }

    .aside{
        padding: 15px 40px;
    }
}

@media (max-width: 576px){
    .aside{
        padding: 15px 30px;
    }
}