.wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 30px;
}

.info{
    margin: 30px 0;
}

.title{
    text-align: center;
    font-weight: 600;
    color: #333333;
    margin-bottom: 10px;
}

.subtitle{
    text-align: center;
    font-size: 14px;
    color: #828282;
    max-width: 400px;
}

.btn{
    background: #2585CD;
    border-radius: 25px;
    font-size: 14px;
    color: #FFFFFF;
    padding: 15px 70px;
}