.wrapper{
    display: flex;
    column-gap: 30px;
}

.info,
.form{
    width: 100%;
}

.info{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.img{
    margin-bottom: 30px;
}

.title{
    margin-bottom: 10px;
    text-align: center;
    font-weight: 600;
    color: #333333;
}

.subtitle{
    text-align: center;
    font-size: 14px;
    color: #828282;
}

.item{
    margin-bottom: 20px;
    position: relative;
}

.input,
.textarea{
    border: 1px solid #E0E0E0;
    border-radius: 15px;
    padding: 15px;
    width: 100%;
    font-size: 14px;
    color: #333333;
}

.input::placeholder,
.textarea::placeholder{
    color: #BDBDBD;
}

.text{
    position: absolute;
    top: 0;
    left: 15px;
    font-size: 14px;
    color: #828282;
    transform: translateY(-50%);
    padding: 0 10px;
    background-color: #fff;
}

.square{
    height: 20px;
    width: 20px;
    border-radius: 5px;
    border: 1.5px solid #BDBDBD;
    position: relative;
    background-color: #fff;
    transition: .5s background-color;
}

.square::before{
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    width: 10px;
    height: 10px;
    transform: translate(-50%, -50%);
    background: url("../../../assets/icons/check.svg") center/contain no-repeat;
    opacity: 0;
    transition: .25s opacity;
}

.checkbox:checked ~ .label .square::before{
    opacity: 1;
}

.checkbox:checked ~ .label .square{
    background-color: #2585CD;
    border: none;
}

.checkbox{
    display: none;
}

.label{
    cursor: url("../../../assets/img/cursor.png");
    display: flex;
    align-items: center;
    column-gap: 10px;
}

.wrap{
    margin-bottom: 30px;
}

.btns{
    display: flex;
    column-gap: 15px;
}

.btn{
    width: 100%;
    background: #2585CD;
    border-radius: 25px;  
    padding: 15px 10px;  
    color: #fff;
}

.gray{
    background: #F2F2F2;
    color: #828282;
}

.error{
    margin-top: 5px;
    color: red;
    font-size: 12px;
}

