.sort{
    display: flex;
    align-items: center;
    column-gap: 30px;
}

.wrap{
    position: relative;
}

.button{
    display: flex;
    align-items: center;
    column-gap: 10px;
    border: 1px solid #E0E0E0;
    border-radius: 15px;
    padding: 15px 30px;
}

.caption{
    font-size: 14px;
    color: #828282;
}

.img{
    transition: .5s transform;
}

.button.active .img{
    transform: rotate(180deg);
}

.items{
    position: absolute;
    z-index: 2;
    bottom: -1px;
    left: 0;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 2px 14px rgba(0, 0, 0, 0.04);
    border-radius: 10px;
    transform: translateY(100%);
    opacity: 0;
    transition: .5s opacity;
    pointer-events: none;
}

.items.active{
    opacity: 1;
    pointer-events: all;
}

.item{
    display: block;
    text-align: start;
    padding: 15px 30px;
    width: 100%;
}

.item.active{
    color: #828282;
}

@media (max-width: 1200px){
    .button,
    .item{
        padding: 15px 25px;
    }
}

@media (max-width: 992px){
    .button,
    .item{
        padding: 15px 20px;
    }
}

@media (max-width: 762px){
    .button,
    .item{
        padding: 10px 15px;
    }

    .text,
    .item{
        font-size: 14px;
    }

    .img{
        height: 20px;
    }

    .img img{
        height: 100%;
    }

    .sort{
        width: 100%;
        justify-content: space-between;
    }
}

